* {
  font-family: Arial, Helvetica, sans-serif;

}
body {
  padding: 60px;
  margin: 0;
}

input {
  width: 100%;
}

.card-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}
.card {
  display: flex;
  flex-direction: column;
  width: 400px;
  max-height: 450px;
  border: 1.5px solid gray;
  padding: 15px;
  margin: 1em;
}
